.header {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #8E9775;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
}

.header .cart{
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #8E9775;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
}

.main-image {
    position: absolute;
    width: 100%;
    height: 25rem;
    z-index: 0;
    overflow: hidden;
}

.main-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}