.data {
    margin: 0;
    padding: 0;
    max-height: 20rem;
    overflow: auto;
    text-align: left;
    font-size: 1.5rem;
}

.dataForm{
    display: flex;
    justify-content: space-between;
}

.dataForm input{
    border-radius: 20px;
    border: 2px solid #4A503D;
}

.total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 1.5rem;
    margin: 1rem 0;
}

.actions {
    text-align: right;
}

.actions button {
    font: inherit;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid #4A503D;
    padding: 0.5rem 2rem;
    border-radius: 25px;
    margin-left: 1rem;
}

.actions button:hover,
.actions button:active {
    background-color: #4A503D;
    border-color: #4A503D;
    color: white;
}


.actions .button--alt {
    color: #4A503D;
}

.actions .button {
    background-color: #4A503D;
    color: white;
}
.cart-items {
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 20rem;
    overflow: auto;
    text-align: left;
    justify-content: space-between;
}
.barang{
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
}