.form {
    text-align: center;

}

.form button {
    font: inherit;
    cursor: pointer;
    background-color: #4A503D;
    border: 1px solid #4A503D;
    color: white;
    padding: 0.25rem 2rem;
    border-radius: 20px;
    font-weight: bold;
    line-height: 10px;
}

.form button:hover,
.form button:active {
    background-color: #36392D;
    border-color: #36392D;
}


.form input {
    width: 3rem;
    border-radius: 5px;
    border: 1px solid #ccc;
    font: inherit;
}